<template>
  <section>
    <b-collapse
      class="float-card"
      :style="{right:rightDis, bottom: bottom}"
      animation="slide"
      position="is-bottom"
      :open="isOpen === 1"
      @open="isOpen === 1"
    >
      <template #trigger="props">
        <div role="button">
          <a class="float-abtn">
            <img
              :class="props.open ? 'menu-down' : 'menu-up'"
              src="../assets/images/icon-up.png"
              alt=""
            />
          </a>
        </div>
      </template>
      <div class="float-content">
        <div class="float-cell">
          <img src="../assets/images/icon-15.png" alt="" />
          <span>在线咨询</span>
        </div>
        <div class="float-cell">
          <img src="../assets/images/icon-16.png" alt="" />
          <span>微信</span>
        </div>
        <div class="float-cell">
          <img src="../assets/images/icon-17.png" alt="" />
          <span>留言</span>
        </div>
      </div>
    </b-collapse>
  </section>
</template>

<script>
import Vue from "vue";
import { Collapse, Icon } from "buefy";
Vue.use(Collapse);
Vue.use(Icon);
export default {
  props: {
    right: {
      type: String,
      default: '200px'
    },
    bottom: {
        type: String,
        default: '500px'
    }
  },
  data() {
    return {
      isOpen: 1,
      rightDis: this.right
    };
  },
  created(){
    const sWidth = document.body.clientWidth || window.screen.width
    this.rightDis = (sWidth- 1200)/2 + 'px'
  },
  watch:{
      right:function(v){
          this.rightDis = v
      }
  }
};
</script>
<style lang="scss" scoped>
.float-card {
  width: 70px;
  position: fixed;
  z-index: 199;
  right: 0;
  bottom: 200px;
}
.float-abtn {
  display: block;
  width: 70px;
  height: 50px;
  background: #ccc;
  display: flex;
  align-items: center;
  justify-content: center;
  img {
    width: 25px;
    &.menu-down {
      transform: rotate(180deg);
    }
  }
}
.float-content {
  width: 70px;
  background: #093884;
  padding: 20px 0 5px;
}
.float-cell {
  display: flex;
  align-items: center;
  flex-direction: column;
  margin-bottom: 15px;
  img {
    width: 40px;
    margin-bottom: 5px;
  }
  span {
    color: #fff;
    font-size: $wdr-main-fontSize;
  }
}
</style>
